<template>
  <div>
    <v-container class="">
      <v-row justify="center">
        <v-col cols="12" md="4">
          <v-snackbar color="red accent-2" v-model="snackbar" vertical>
            {{ alertsg_msg }}
            <template v-slot:action="{ attrs }">
              <v-btn text v-bind="attrs" @click="snackbar = false">
                Close
              </v-btn>
            </template>
          </v-snackbar>
        </v-col>
      </v-row>
      <v-row class="mt-16" justify="center">
        <v-col cols="11" md="4">
          <div class="input_Box input_color_Result">
            <h2>
              {{
                inputData.number_1 +
                " " +
                inputData.operation +
                " " +
                inputData.number_2 +
                " " +
                Result
              }}
            </h2>
          </div>

          <input
            v-model="inputData.number_1"
            dark
            placeholder="Number 1"
            class="input_Box input_color_Numb mt-14"
            type="number"
          />
          <input
            placeholder="Number 2"
            v-model="inputData.number_2"
            class="input_Box input_color_Numb mt-4"
            type="number"
          />
        </v-col>
      </v-row>
      <v-row justify="center" class="mt-8 text-center">
        <v-col cols="11" md="4">
          <v-row align="center" justify="space-around">
            <v-btn
              @click="AddSumOperation"
              class="pa-8 btn_style"
              color="#5F27CD"
              dark
              >+</v-btn
            >
            <v-btn
              @click="AddSubOperation"
              class="pa-8 btn_style"
              color="#5F27CD"
              dark
              >-</v-btn
            >
            <v-btn
              @click="ClearInputsFunction"
              class="pa-8 btn_style"
              color="#222F3E"
              dark
              >C</v-btn
            >
          </v-row>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="11" md="4">
          <v-btn
            @click="PrintResultOperation"
            block
            class="mt-6 pa-10 btn_style"
            color="#FECA57"
            >=</v-btn
          >
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "../axios-setting";

export default {
  data: () => {
    return {
      snackbar: false,
      alertsg_msg: "",
      Result: "",
      inputData: {
        number_1: "",
        number_2: "",
        operation: "",
      },
    };
  },
  name: "Home",

  computed: {
    result_fun() {
      return this.Result;
    },
  },
  methods: {
    AddSumOperation() {
      this.inputData.number_1 != "" || this.inputData.number_2 != ""
        ? (this.inputData.operation = "+")
        : (this.alertsg_msg = "Enter numbers first!") & (this.snackbar = true);
    },
    AddSubOperation() {
      this.inputData.number_1 != "" || this.inputData.number_2 != ""
        ? (this.inputData.operation = "-")
        : (this.alertsg_msg = "Enter numbers first!") & (this.snackbar = true);
    },
    ClearInputsFunction() {
      this.Result = "";
      this.inputData.operation = "";
      this.inputData.number_1 = "";
      this.inputData.number_2 = "";
    },
    PrintResultOperation() {
      this.inputData.number_1 != "" || this.inputData.number_2 != ""
        ? this.inputData.operation != ""
          ? axios
              .post("/set/", { inputdata: this.inputData })
              .then((resp) => {
                this.Result = " = " + resp.data;
                console.log(resp.data);
              })
              .catch((err) => {
                console.log(err);
              })
          : (this.alertsg_msg = "Choose a mathematical operation !") &
            (this.snackbar = true)
        : (this.alertsg_msg = "Enter numbers first!") & (this.snackbar = true);
    },
  },
};
</script>
