.input_Box {
  border-radius: 9px;
  width: 100%;
  color: rgb(1, 1, 1);
  height: 80px;
  padding-left: 20px;
  font-size: 2rem;
}
.input_color_Result {
  background-color: #8395a7;
}
.input_color_Numb {
  background-color: #c8d6e5;
}
.btn_style {
  font-size: 2rem !important;
}
</style>